export const compileResultFunction = (answers) => {
  let array = [{ list: [] }];

  for (let i = 0; i < answers.length; i++) {
    if (
      answers[i]?.list?.find((li) => {
        return li?.isChecked || li?.range || li?.answer;
      })
    ) {
      array[0].list.push(
        ...answers[i].list.filter((li) => {
          return li?.isChecked || li?.range || li?.answer;
        })
      );
    }
  }
  return array;
};

