import React, { useEffect, useState } from "react";
import style from "./MainResults.module.css";
import { useSelector } from "react-redux";
import { colors } from "../Utils/utils";

const VizualResultContainer = ({ result, currentCaseName }) => {
  const { questions } = useSelector((state) => state.questions);
  return (
    <section className={style.vizualContainer}>
      <h1 className={style.headCase}>{currentCaseName}</h1>
      {questions.map((el, index) => {
        return (
          <div key={index}>
            <h2 className={style.questionName}>{el.blockName}</h2>
            <div>
              {el.list.map((li, n) => {
                let count = 0;

                for (let i = 0; i < result.length; i++) {
                  if (result[i].list.find((e) => e.name === li.name)) {
                    count++;
                  }
                }
                return (
                  <div key={n}>
                    {count ? (
                      <div className={style.resultVariant}>
                        {li.name}:
                        <span
                          style={{
                            display: "block",
                            color: "black",
                            fontWeight: "bold",
                            background: `linear-gradient(-20deg, ${"white"}, ${
                              colors[n]
                            })`,
                            width: `${count < 80 ? count * 3 : 80}vw`,
                            textAlign: "center",
                            transition: "2s",
                            borderBottomRightRadius: "20px",
                            borderBottomLeftRadius: "5px",
                            borderTopRightRadius: "5px",
                            borderTopLeftRadius: "20px",
                          }}
                        >
                          {count ? count : null}
                        </span>
                      </div>
                    ) : null}
                  </div>
                );
              })}
            </div>
          </div>
        );
      })}
    </section>
  );
};

export default VizualResultContainer;
