import React from "react";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CSVLink } from "react-csv";
import { getCurrentResult } from "../../DAL/dal";
import style from "./Excel.module.css";
import PDFComponent from "./PDFComponent/PDFComponent";

const Excel = ({ caseName, result, pdf, setPdf }) => {
  return (
    <>
      {pdf ? (
        <PDFComponent />
      ) : (
        <div className={style.linkContainer}>
          <CSVLink className={style.csvLink} data={result} filename={caseName}>
            Скачать CSV файл
          </CSVLink>
          <div
            onClick={() => {
              setPdf(true);
            }}
            className={style.pdfLink}
          >
            Скачать PDF файл
          </div>
        </div>
      )}
    </>
  );
};

export default Excel;
