import axios from "axios";

// export const URL = "http://localhost:5000/";
export const URL = "https://congress.pep-server.ru/";

const instance = axios.create({
  baseURL: URL,
});

export const sendResult = (result) => {
  return instance.post("set-data", { result }).then((response) => {
    return response;
  });
};

export const getCurrentCase = () => {
  return instance.get("get-current-case").then((response) => response);
};

export const getAllCases = () => {
  return instance.get("get-all-cases").then((response) => response);
};
export const getCurrentResult = () => {
  return instance.get("get-current-result").then((response) => response);
};
