import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { compileResultFunction } from "../Utils/utils";
import style from "./allFinalStyle.module.css";
import mero from "../../Images/MERO_Place.png";
import Preloader from "../preLoader/Preloader";

export const SenderComponent = ({ socket, currentCaseName }) => {
  const { answers } = useSelector((state) => state.answers);
  const requestFirst = compileResultFunction(answers);

  return (
    <>
      <SendResult
        currentCaseName={currentCaseName}
        socket={socket}
        requestFirst={requestFirst}
      />
    </>
  );
};

const SendResult = ({ requestFirst, socket, currentCaseName }) => {
  const [num, setNum] = useState(0);
  const [isSuccess, setIsSuccess] = useState(false);

  useEffect(() => {
    socket.emit("sendResult", requestFirst);

    // if (!window.localStorage.getItem("cases")) {
    //   window.localStorage.setItem("cases", [currentCaseName]);
    // } else {
    //   let storage = window.localStorage.getItem("cases");
    //   window.localStorage.setItem("cases", [storage, currentCaseName]);
    //   console.log(storage);
    // }
    setIsSuccess(true);
  }, []);
  // useEffect(() => {
  //   let interval = setInterval(() => {
  //     socket.emit("sendResult", requestFirst);
  //   }, 10);
  //   setTimeout(() => {
  //     clearInterval(interval);
  //   }, 1000);
  // }, []);

  return (
    <>
      {!isSuccess ? (
        <div style={{ textAlign: "center" }}>
          <h2 className={style.finalH2}>
            Отправка данных займет некоторое время, пожалуйста подождите
          </h2>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              marginBottom: "20px",
            }}
          >
            <Preloader />
          </div>
          {num == 0 ? (
            <div>Отправляем Ваши Результаты...</div>
          ) : num == 1 ? (
            <div>Внимательно проверяем все данные...</div>
          ) : num == 2 ? (
            <div>Считаем общее колличество участников...</div>
          ) : (
            <div>Еще буквально несколько секунд...</div>
          )}
        </div>
      ) : (
        <div style={{ textAlign: "center" }}>
          <h3>Успешно!</h3>
        </div>
      )}
      <div className="poweredByMero">
        <span>Сделано на платформе</span>
        <img src={mero} alt="" />
      </div>
    </>
  );
};
