import React, { useEffect } from "react";
import style from "./PDFComponent.module.css";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

const PDFComponent = ({ result, setPdf, caseName }) => {
  useEffect(() => {
    const downloadPDF = () => {
      const capture = document.getElementById("pdf");
      html2canvas(capture).then((canvas) => {
        const imgData = canvas.toDataURL("img/png");
        const doc = new jsPDF("p", "mm", "a4");
        const componentWidth = doc.internal.pageSize.getWidth();
        const componentHeight = doc.internal.pageSize.getHeight();
        doc.addImage(imgData, "PNG", 0, 0, componentWidth, componentHeight);
        doc.save(`${caseName}.pdf`);
        setPdf(false);
      });
    };
    downloadPDF();
  }, []);
  return (
    <section id="pdf" className={style.mainPDFContainer}>
      {result?.map((el, index) => {
        const colors = ["red", "blue", "green", "grey", "yellow"];
        return Object.entries(el).map((li, num) => (
          <div style={{ background: colors[index], padding: "5px" }} key={num}>
            <h3
              className={style.pdfParamName}
              style={
                li[0] === "вопросы"
                  ? {
                      backgroundColor: "bisque",
                      padding: "5px 10px",
                      borderRadius: "10px",
                      color: "rgb(53, 50, 50)",
                    }
                  : {}
              }
            >
              {li[0] === "вопросы" ? "Вопрос:" : li[0] + ":"}

              <span className={style.pdfParamValue}>{" " + li[1]}</span>
            </h3>
          </div>
        ));
      })}
    </section>
  );
};

export default PDFComponent;
